@import "vars";

@media screen and(max-width: $tablet_bp) {

  .mob {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    
    height: 100px;
    
    font-family: Poppins, Arial, Helvetica, sans-serif;
    font-size: 20px;
    z-index: 1;

    &__nav {
      display: block;

      position: absolute;
      right: -390px;
      top: 0;
    
      width: 390px;
      height: 645px;
      border-radius: 30px;
    
      background-color: $primarycolor;
      transition: transform 0.3s ease-in-out;
      z-index: 1;
    }

    &__list {
      display: flex;
      justify-content: space-around;
      height: 80%;
      flex-direction: column;
      list-style: none;
    }

    &__slide {
      transform: translate(-390px);
    }

    &____item {
      max-width: 250px;
    }

    &__hire {
      color: #fff;
      padding-bottom: 20px;
      border-bottom: 1px solid $secondcolor;
    
    }
  
  }

}

@media screen and(max-width: $small_screen) {
  
  .mob {

    &__nav {
      right: -360px;
      width: 360px;
      height: 550px;
    }

    &__list {
      width: 310px;
      align-items: center;
    }

    &__slide {
      transform: translate(-360px);
    }

    &__hire {
      margin-left: 120px;
    }
  
  }

}

@media screen and (max-width: $small_screen-xs) {

  .mob {

    &__nav {
      right: -320px;
      max-width: 320px;
      height: 500px;
    }

    &__slide {
      transform: translate(-320px);
    }
  }
}
